// React Package Module
import { useState } from "react";

// MUI Package Module
import { Box, styled, TextField } from "@mui/material";

// Control Component
import Buttons from "components/controls/Button";

// Constant
import { COLOR, LABEL, CODE, MESSAGE } from "common";

// util
import { idValidation, pwValidation } from "common/util";

// API
import { postData } from "common/communication";

/** STYLE Ï†ïÏùò */
// Ï†ÑÏ≤¥ ÏòÅÏó≠
const Wrapper = styled(Box)({
	width: "99%",
	height: "100%",
	display: "flex",
	justifyContent: "center",
	alignItems: "center",
  boxSizing: "border-box",
});

// Î°úÍ∑∏Ïù∏ Ï†ÑÏ≤¥ ÏòÅÏó≠
const LoginBox = styled(Box)({
	width: 400,
  marginTop: -39
});

const Text = styled(TextField)({
	width: "100%",
	"& .MuiInputBase-input": {
		height: 40,
		fontSize: 17,
	},
});

/** ÌöåÏõêÍ∞ÄÏûÖ ÌåùÏóÖ Ïª¥Ìè¨ÎÑåÌä∏ (Î°úÍ∑∏Ïù∏ Ïª¥Ìè¨ÎÑåÌä∏ ÎÇ¥ ÌöåÏõêÍ∞ÄÏûÖ Î≤ÑÌäº ÌÅ¥Î¶≠ Ïãú Ìï¥Îãπ ÌåùÏóÖ ÎùÑÏõåÏ§å) */
const JoinPopup = () => {

	const [id, setId] = useState("");               // ÏûÖÎ†•Ìïú ÏïÑÏù¥Îîî
	const [pw, setPw] = useState("");               // ÏûÖÎ†•Ìïú ÎπÑÎ∞ÄÎ≤àÌò∏
	const [idRegMsg, setIdRegMsg] = useState("");   // ÏïÑÏù¥Îîî Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨ ÎØ∏ÌÜµÌôî Ïãú ÎùÑÏõåÏ£ºÎäî ÏóêÎü¨Î©îÏÑ∏ÏßÄ
	const [pwRegMsg, setPwRegMsg] = useState("");   // ÎπÑÎ∞ÄÎ≤àÌò∏ Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨ ÎØ∏ÌÜµÌôî Ïãú ÎùÑÏõåÏ£ºÎäî ÏóêÎü¨Î©îÏÑ∏ÏßÄ

	// inputÍ∞í ÏûÖÎ†•
	const inputId = (e) => {
		setId(e.target.value);
	};

	const inputPw = (e) => {
		setPw(e.target.value);
	};

	const validation = (type) => {
		if (type === "id") {
			setIdRegMsg(idValidation(id));
		} else if (type === "pw") {
			setPwRegMsg(pwValidation(pw));
		}
	};

	const onJoin = () => {

    // Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨ ÌÜµÍ≥º Î™ªÌïòÍ±∞ÎÇò ÏûÖÎ†•Ìïú id, pwÍ∞íÏù¥ ÏóÜÏúºÎ©¥ Í≤ΩÍ≥† Î©îÏÑ∏ÏßÄ ÎùÑÏõåÏ£ºÍ≥† Ìï®Ïàò Ï¢ÖÎ£å
    if (idRegMsg !== '' || pwRegMsg !== '' || id == '' || pw == '') {
      alert(MESSAGE.ERROR.CHECK_JOIN_INFO);
      return;
    }
		postData("auth/join", {
			login_id: id,
			login_pw: pw,
		})
			.then((data) => {
				if (typeof data === "string") {
					alert(MESSAGE.ERROR.EXIST_USER);
				} else if (typeof data === "object") {
					localStorage.setItem("profile", JSON.stringify(data));
					alert(MESSAGE.JOINED);
					window.location.reload();
				}
			})
			.catch((err) => {
				console.log(err);
			});
	};

  const enter = (e) => {
    if (e.key === "Enter") onJoin();
  };


	return (
		<>
			<Wrapper>
				<LoginBox>
					<Text
						fullWidth
						id="fullWidth"
						variant="standard"
						placeholder="ID"
						onChange={inputId}
						onBlur={() => validation("id")}
						value={id}
						helperText={idRegMsg}
					/>
					<Text
						fullWidth
						id="fullWidth"
						variant="standard"
						placeholder="PW"
						onChange={inputPw}
						onBlur={() => validation("pw")}
						value={pw}
						type="password"
						sx={{
							marginBottom: 5,
						}}
						helperText={pwRegMsg}
            onKeyDown={enter}
					/>
					<Buttons
						type={CODE.BUTTON.BORDER}
						name={LABEL.BUTTONS.JOIN_BTN}
						backgroundColor={COLOR.WHITE}
						color={COLOR.BLACK}
						width="100%"
						height="40px"
						padding="3px"
						fontSize="20px"
						onSubmit={onJoin}
					/>
				</LoginBox>
			</Wrapper>
		</>
	);
};

export default JoinPopup;
