import { useState } from "react";

// MUI Package Module
import { Dialog, DialogContent, createTheme, ThemeProvider } from "@mui/material";
import CloseIcon from '@mui/icons-material/Close';

/** Î™®Îì† ÌåùÏóÖ Ïª¥Ìè¨ÎÑåÌä∏Ïùò Î∂ÄÎ™®Í∞Ä ÎêòÎäî Í∏∞Î≥∏ Î™®Îã¨ Ïª¥Ìè¨ÎÑåÌä∏ */
const ModalPopup = (props) => {
  // fullWidth ÏÜçÏÑ± propsÏóê Ï∂îÍ∞ÄÌïòÏó¨ fullWidthÏùº ÎïåÎßå ÎÑàÎπÑ 100%Î°ú ÏÑ§Ï†ï (novel-detail ÏÜåÏÑ§ Î≥¥Í∏∞, Ïì∞Í∏∞)
  const { children, height, onClose, open, width, popupState, fullWidth, mode } = props;

  const [colorMode, setColorMode] = useState('#ffffff');

  // Î™®Îì† Î™®Îã¨Ï∞Ω Îí∑ Î∞∞Í≤Ω Ìà¨Î™ÖÎèÑ Ï°∞Ï†àÏùÑ ÏúÑÌïú ÌÖåÎßà Í∞ùÏ≤¥ ÏÉùÏÑ± (Ï†ÑÏ≤¥ Ïä§ÌÉÄÏùºÎßÅ Ï†ÅÏö©)
  const theme = createTheme({
    components: {
      MuiDialog: {
        styleOverrides: {
          root: {
            backgroundColor: 'rgba(0, 0, 0, 0.7)', 
          },
        },
      },
    },
  });

  // Esc Î≤ÑÌäº ÎàÑÎ•º Ïãú Î™®Îã¨ Ï∞Ω Îã´Í∏∞
  const pressEsc = (e) => {
    if (e.key === 'Escape') {
      onClose();
      setColorMode("#ffffff");
      props.setColorInit && props.setColorInit();
    }
  };

  const brightDark = (click) => {
    let color;
    switch (click) {
      case 1:
        color = "#ffffff";
        break;
      case 2:
        color ="#f2e8cf";
        break;
      case 3:
        color = "#121212";
        break;
      default:
        color = "#ffffff";
    }
    setColorMode(color);
    props.setColor(color);
  };

  return ( 
    <ThemeProvider theme={theme} >
      <Dialog
        onKeyDown={pressEsc} 
        open={open} 
        fullWidth={fullWidth}
        PaperProps={{style:{maxWidth:'100%', width: width, backgroundColor: colorMode, color: colorMode === "#121212" ? "white" : "black"}}}> 
          {/* CloseIconÏùÑ ÎàåÎ†ÄÏùÑ Îïå joinÌåùÏóÖÏùº Í≤ΩÏö∞ Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê profileÏù¥ ÏûàÏúºÎ©¥ ÏÉàÎ°úÍ≥†Ïπ®. */}
          {mode && 
            <div style={{ display: "flex", margin: "10px 0 0 10px", gap: 5 }}>
              <button style={{ width: 18, height: 18, borderRadius: '50%', backgroundColor: "#ffffff" }} onClick={() => brightDark(1)}></button>
              <button style={{ width: 18, height: 18, borderRadius: '50%', backgroundColor: "#f2e8cf" }} onClick={() => brightDark(2)}></button>
              <button style={{ width: 18, height: 18, borderRadius: '50%', backgroundColor: "#121212" }} onClick={() => brightDark(3)}></button>
            </div>
          }
          <CloseIcon 
            sx={{ 
              padding: "11px 13px 0px 0px", 
              marginLeft: "auto", 
              position: "absolute", 
              right: 10, top: 3, 
              color: colorMode === "#121212" ? "white" : "black",
              ":hover": {
                cursor: "pointer"
              } 
            }}
            onClick={() => {
              onClose();
              setColorMode("#ffffff");
              props.setColorInit && props.setColorInit();
              popupState === "join" && localStorage.getItem("profile") && window.location.reload();
            }}
          />
        <DialogContent 
          sx={{
            boxSizing: 'border-box',
            width: fullWidth ? '100%' : width, 
            height: height,
            backgroundColor: colorMode
          }}
        >
          {children}
        </DialogContent>
      </Dialog>
    </ThemeProvider>
  )
};

export default ModalPopup;